.fadeIn {
  animation: fadeInAnimation 0.25s ease;
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
    transform: scale(0%);
  }
  50% {
    transform: scale(110%);
  }
  100% {
    opacity: 1;
    transform: rotate(0);
    transform: scale(100%);
  }
}

.spinOut {
  animation: spinOutAnimation 0.8s ease-in-out;
}

@keyframes spinOutAnimation {
  0% {
    transform: rotate(-30deg);
  }
  50% {
    /* transform: scale(1); */
    transform: rotate(30deg);
  }
  100% {
    /* transform: scale(0); */
    transform: rotate(0deg);
  }
}
