/* SlotMachine.css */

.slot-machine {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.spin-button {
  height: 100vh;
  width: 23vw;
  border-radius: 0 !important;

  /* Set the position property to 'fixed' to position the button relative to the viewport */
  position: fixed;

  /* Set the button to the right side of the screen */
  right: 0;
  border: none;
  cursor: pointer;
}

.reel {
  font-size: 80px;
  font-family: "Santa";
}

.button-text {
  font-family: "Chrysler";
  font-size: 40px;
}


.btn-grad {
  background-image: linear-gradient(to right, #994c4c 0%, rgb(181, 73, 73)  51%, #994c4c  100%);
  padding: 15px 45px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  display: block;
}

.btn-grad:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
